  opacity: 1;
  -webkit-animation: blink 3s ease infinite;
          animation: blink 3s ease infinite;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.fab-dots-1 {
  left: 15px;
  -webkit-animation-delay: 0s;
